@import url(https://fonts.googleapis.com/css?family=Karla:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: "Karla", sans-serif, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black; }

.App {
  display: flex;
  flex-direction: column;
  height: 100vh; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

button {
  background-color: white; }

main {
  display: flex;
  justify-content: center;
  flex: 1 0; }

.grower {
  flex-grow: 5;
  min-height: 5px; }

.fix-top {
  flex: 0 0 60px; }

.fix-bottom {
  flex: 0 0 60px; }

footer {
  background-color: white; }

.nav-style {
  color: black;
  background-color: white;
  z-index: 3;
  margin: 0;
  padding: 0;
  height: 60px;
  display: flex;
  align-content: center;
  justify-content: center;
  width: 100vw;
  border-radius: 0px; }
  .nav-style ul {
    display: flex; }
  .nav-style a {
    display: inline;
    color: black;
    text-decoration: none;
    align-self: center;
    justify-self: center; }
    .nav-style a :hover {
      color: pink; }

.nav-link {
  align-self: center;
  justify-self: center;
  font-family: "Karla", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  padding: 0 10px; }

.nav-width {
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  flex-direction: row;
  justify-content: space-between; }

#top-nav {
  position: fixed;
  top: 0; }

#bottom-nav {
  position: fixed;
  bottom: 0; }

section {
  width: 90vw;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center; }

.landing-wrapper {
  display: flex;
  flex-direction: row;
  align-self: center;
  justify-self: center;
  padding: 20px 0; }

.landing-text {
  font-family: "Karla", sans-serif;
  text-align: center;
  font-size: 3.5rem;
  color: black; }

.landing-random {
  display: inline; }

.action {
  font-weight: 400; }

.object {
  font-weight: 700; }

.lang-button {
  align-self: center;
  background-color: black;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  text-align: center;
  font-family: "Karla", sans-serif;
  font-size: 10px;
  font-weight: 700; }

@media only screen and (max-width: 800px) {
  .landing-text {
    font-size: 2rem; } }

.all-projects {
  width: 100vw;
  max-width: 900px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  margin: 0 35px; }

.project-img-feed {
  object-fit: cover;
  width: 10vw;
  height: 10vw; }

.project-in-feed {
  align-self: center;
  width: 10vw;
  margin: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }

.project-detail {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center; }

.projects-title {
  font-size: 15px;
  text-align: center;
  text-align: center; }

.project-description {
  text-align: center; }

.project-context {
  text-align: center;
  display: inline; }

.project-img {
  object-fit: contain;
  max-width: 40vw;
  max-height: 40vw;
  align-self: center; }

.project-title {
  text-align: center; }

.project-subtitle {
  text-align: center; }

.project-author {
  text-align: center; }

.project-category {
  text-align: center; }

.project-tags {
  text-align: center; }

